const KYCPage = () => {

  const devices = [
    // { id: '1', img: imgg1, name: 'Iris' },
    { id: '2', img: imgg2, name: 'Mantra' },
    { id: '3', img: imgg3, name: 'Mantra L1' },
    { id: '4', img: imgg4, name: 'Morpho' },

  ]
  const { walletCall, wadhValue, selectedBank, mobile, setCurrentPage } = useBankTransfer()
  const [selectedDeviceId, setSelectedDeviceId] = useState(null);

  const { loading, captureFingerprint } = useFingerprintCapture({ wadhValue });

  const handleDeviceSelect = (id) => {
    setSelectedDeviceId(id);

  };

  const selectedDevice = devices.find((d) => d.id === selectedDeviceId);

  const handleCaptureFingerprint = withLocation(async (latitude, longitude) => {
    if (!selectedDevice) return alert('Please select a device.');

    try {
      const result = await captureFingerprint(selectedDevice.name);
      const xmlStr = result.pidData + '<bookz/>';
      const encodedXml = window.btoa(new XMLSerializer().serializeToString(
        new DOMParser().parseFromString(xmlStr, 'application/xml')
      ));

      const body = {
        customer_mobile: mobile,
        latitude,
        longitude,
        biometric_data: encodedXml,
        api_id: selectedBank,
      };

      const res = await dmtkycapi(body);
      if (res?.data?.statusCode === '200' && !res?.data?.error) {
        setCurrentPage('send-money-page');
        walletCall()
      }
      toastMessage(res?.data?.error, res?.data?.message);
    } catch (error) {
      alert('Capture failed: ' + error.message);
    }
  });



  return (
    <>
      <CustomBox>
        <Box display="flex" alignItems="center" gap={2} mb={2}>
          <IconButton onClick={() => setCurrentPage('money-transfer')}>
            <ArrowBackIosNewIcon />
          </IconButton>
          <Typography
            variant="body2"
            color="var(--bs-nav)"
            fontWeight="bold"
            fontSize={18}
          >
            Verify KYC
          </Typography>
        </Box>

        <Grid container spacing={2} sx={{ p: 2 }}>
          <Grid item xs={12}>
            <Grid container spacing={2}>
              {devices?.map((device, index) => (
                <Grid item xs={6} sm={4} md={2} key={index}>
                  <Card
                    onClick={() => handleDeviceSelect(device.id)}
                    sx={{
                      display: 'flex',
                      flexDirection: 'column',
                      alignItems: 'center',
                      p: 1,
                      height: '100%',
                      border: selectedDeviceId === device.id ? '1px solid var(--bs-nav)' : '1px solid #ddd',
                      borderRadius: 2,
                      boxShadow: 0,
                      cursor: 'pointer',
                      transition: 'all 0.3s ease',
                      // backgroundColor: selectedDeviceId === device.id ? '#e3f2fd' : '#fff',
                    }}
                  >
                    <CardMedia
                      component="img"
                      image={device.img}
                      alt={device.name}
                      sx={{ height: 80, objectFit: 'contain', mb: 1 }}
                    />
                    <CardContent sx={{ p: 0 }}>
                      <Typography
                        variant="body2"
                        align="center"
                        sx={{ fontWeight: 500 }}
                      >
                        {device.name}
                      </Typography>
                    </CardContent>
                  </Card>
                </Grid>
              ))}
            </Grid>
          </Grid>

          <Grid item xs={12} textAlign="center">
            <Button
              variant="contained"
              disabled={!selectedDeviceId || loading}
              onClick={handleCaptureFingerprint}

              sx={{
                borderColor: 'var(--bs-primary1)', // Custom colors
                mt: 3,
                backgroundColor: 'var(--bs-primary1)', // Custom colors
                color: 'white'
              }}

            >
              {loading ? 'Capturing...' : 'Capture Fingerprint'}
            </Button>
          </Grid>
        </Grid>
      </CustomBox>
    </>
  )
}


const InvoiceRecord = () => {
  const [loading, setLoading] = useState(false);

  const { invoiceId, setCurrentPage } = useBankTransfer()
  const [data, setData] = useState(null)





  const ref = useRef()

  const handlePrint = useReactToPrint({
    content: () => ref.current,
    documentTitle: 'DMT Data',
    // onAfterPrint: () => alert('Print Success')
  })

  const handleDownloadThermal = () => {
    const element = document.getElementById('contentId1');
    const opt = {
      margin: 1,
      format: [90, 297],
      filename: 'thermal_print_page.pdf',
      image: { type: 'jpeg', quality: 0.98 },
      html2canvas: { scale: 2 },
      jsPDF: { unit: 'mm', format: [110, 200], orientation: 'portrait' }
    };
    // Generate PDF
    html2pdf().set(opt).from(element).save();
  }



  const dataDetails = async () => {
    setLoading(true)
    try {
      const res = await dmtDtails(invoiceId)
      setData(res?.data?.data);
    } catch (error) {

    }
    setLoading(false)
  }
  useEffect(() => {
    dataDetails()


  }, [invoiceId])

  const scrollRef = useRef(null);

  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = 0;
    }
  }, []);


  const executeScroll = () => {
    scrollRef?.current?.scrollIntoView({
      behavior: 'smooth',
      block: 'end',
    });
  };

  useEffect(() => {
    executeScroll()
  }, [])
  return (
    <>

      {loading && <Loader />}
      <div ref={scrollRef}></div>
      <div className="ContentArea" >
        <div className="card">
          <div className="card-header card-Details">
            <div className='d-flex align-items-center'>
              <IconButton onClick={() => setCurrentPage('send-money-page')


              }>
                <ArrowBackIosNewIcon />

              </IconButton>
              <div className="Details"><span>Details</span></div>
            </div>

            <div className="button-print">
              <div type="button" className="btn btn-warning mr-3" onClick={handlePrint}
              > <IoMdPrint />Full Print</div>
              {/* <div type="button" className="btn btn-warning mr-3" onClick={handleDownloadShort}> <IoMdPrint />Short Print</div> */}
              <div type="button" className="btn btn-warning mr-3" onClick={handleDownloadThermal}> <IoMdPrint />Thermal Print</div>
            </div>
          </div>
          <div ref={ref} id="contentId1" className="contentId2">
            <div className="card-body">
              <div className="row">
                <div className="col-lg-12">
                  <div className="logo-print text-align-center pb-3">
                    <img src={image} alt="" />
                  </div>
                </div>
                <div className="col-lg-6 aabc">
                  <div className="left-side-section">
                    <div>CSP Name : <span>{data?.invoice[0]?.shop_name}</span></div>
                    <div>Mobile Number : <span>{data?.invoice[0]?.mobile_number}</span></div>
                    <div>Sender Name : <span>{data?.invoice[0]?.customer_name}</span></div>
                    <div>Sender No : <span>{data?.invoice[0]?.customer_mobile}</span></div>
                  </div>
                </div>
                <div className="col-lg-6 aabc">
                  <div className="right-side-section">
                    <div>Beneficiary : <span>{data?.invoice[0]?.beneficiary_name}</span></div>
                    <div>Bank Name : <span>{data?.invoice[0]?.bank_name}</span></div>

                    <div>Account No : <span>{data?.invoice[0]?.account_number}</span></div>
                    <div>Date : <span>{data?.invoice[0]?.createdAt}</span></div>
                  </div>
                </div>
              </div>
            </div>

            <div className="col-lg-12">
              <div className="card mt-4" style={{ overflow: 'auto' }}>
                <div className="card-body">
                  <div id="myTable_wrapper" className="dataTables_wrapper no-footer">
                    <table className="table table-striped table-bordered table-hover display dataTable no-footer dtr-inline collapsed" role="grid" aria-describedby="myTable_info" >
                      <thead>
                        <tr role="row">
                          <th className="sorting" >TID</th>
                          {/* <th className="sorting">
                                                          MODE
                                                      </th> */}
                          <th className="sorting" >
                            Status
                          </th>
                          <th className="sorting" >UTR No</th>
                          <th className="sorting" >Amount</th>
                        </tr>
                      </thead>
                      <tbody>
                        {data && data?.invoice[0]?.allTrans?.map((item) => {
                          return <tr key={item?._id}>
                            <td valign="top" className="dataTables_empty">{item?.tid}</td>

                            <td valign="top" className="dataTables_empty">{item?.status}</td>
                            <td valign="top" className="dataTables_empty">{item?.utr}</td>
                            <td valign="top" className="dataTables_empty">{item?.amount}</td>
                          </tr>
                        })}
                        <tr>
                          <td valign="top" className="dataTables_empty" colSpan={3}>Total</td>
                          <td valign="top" className="dataTables_empty">{data?.invoice[0]?.totalAmount}</td>

                        </tr>


                      </tbody>
                    </table>
                  </div>

                  <div className="col-lg-12">
                    <div className="reserve">
                      <p> {new Date()?.getFullYear()} All Rights Reserved </p>
                    </div>
                  </div>

                </div>
              </div>
            </div>
          </div>

        </div>
      </div>


    </>
  )
}




const NewBankTransfer = () => {
  const [suggestions, setSuggestions] = useState([]);
  const [error, setError] = useState('');

  const { globalLoader, setGlobalLoader, mobile, setMobile, setCurrentPage, currentPage, setCustomer, selectedBank, setSelectedBank, allBank, setAllBank } = useBankTransfer()



  const fetchSuggestions = useCallback(async () => {
    try {
      const response = await sagestMobileNumber({ mobile });
      const numbers = response?.data?.data || [];
      setSuggestions(numbers);
    } catch (err) {
      console.error('Error fetching suggestions:', err);
      setSuggestions([]);
    }
  }, [mobile]);

  useEffect(() => {
    if (!mobile) {
      setSuggestions([]);
      return;
    }

    const timeout = setTimeout(fetchSuggestions, 500);
    return () => clearTimeout(timeout);
  }, [mobile, fetchSuggestions]);

  const handleInputChange = (_, newValue) => {
    // Clean non-digits and limit to 10 characters
    const val = newValue.replace(/\D/g, '').slice(0, 10);

    setMobile(val); // Update only trimmed value

    // Now validate the trimmed value
    if (!val) {
      setError('');
    } else if (val.length < 10 || !isValidIndianMobile(val)) {
      setError('Enter a valid 10-digit mobile number.');
    } else {
      setError('');
    }
  };


  const handleSearch = async (e) => {
    e.preventDefault()
    if (!isValidIndianMobile(mobile)) {
      setError('Please enter a valid mobile number.');
      return;
    }
    setGlobalLoader(true)
    let body = {

      mobile: mobile,
      api_id: selectedBank
    }
    try {
      let res = await CustomerInfo(body)
      if (res?.data?.statusCode === '401') {
        setCurrentPage('add-customer')
      }
      if (res?.data?.statusCode === '008')
        setCurrentPage('otp-page')

      if (res?.data?.statusCode === '007') {
        setCurrentPage('marchant-onboarding-otp')
      }
      if (res?.data?.statusCode === '200') {
        setCustomer(res?.data?.data?.response)
        setCurrentPage('send-money-page')
      }

      toastMessage(res?.data?.error, res?.data?.message)
    } catch (error) {

    }
    finally {
      setGlobalLoader(false)
    }

  };


  const getBank = async () => {
    let res = await settingBank()
    setAllBank(res?.data?.data?.dmtApiType || []);
    setSelectedBank(res?.data?.data?.defaultApi)
  }

  useEffect(() => {
    getBank()
  }, [])

  const handleBankChange = (id) => {
    // navigate('/bank-transfer')
    setSelectedBank(id)
  }
  return (

    <>
    
      {globalLoader && <Loader />}
      <div className="PageHeading"><h1>Bank Transfer</h1></div>

      {currentPage === 'money-transfer' &&
        <>
          <CustomBox

          >
            <Typography variant="h6" fontWeight="600" mb={2}>
              Bank Selection
            </Typography>
            <Grid container spacing={2}>
              {allBank?.map((bank) => {
                const isSelected = selectedBank === bank._id;

                return (
                  <Grid item xs={6} sm={3} md={3} lg={2} key={bank._id}>
                    <Card
                      elevation={isSelected ? 6 : 1}
                      sx={{
                        border: isSelected ? '1px solid var(--bs-nav)' : '1px solid #ccc',
                        position: 'relative',
                        borderRadius: 2,
                        transition: 'all 0.3s ease',
                        width: '150px'
                        //   backgroundColor:'#F8F9FA'
                      }}
                    >
                      <CardActionArea onClick={() => handleBankChange(bank._id)}>
                        <CardContent sx={{
                          p: 1
                        }}>
                          <Typography
                            variant="subtitle1"
                            align="center"
                            color='var(--bs-nav)'

                            fontSize={14}

                          >
                            {bank.name.toUpperCase()}
                          </Typography>
                        </CardContent>
                        {isSelected && (
                          <CheckCircle
                            sx={{
                              position: 'absolute',
                              top: 11,
                              right: 30,
                              width: '17px',
                              height: '17px',
                              color: '#1976d2',
                              fill: 'var(--bs-nav)'
                            }}
                          />
                        )}
                      </CardActionArea>
                    </Card>
                  </Grid>
                );
              })}
            </Grid>
            <Box
              sx={{
                // width:'50%',
                mt: 2,
                width: { xs: '100%', sm: '50%' }
                //   maxWidth: 800,

              }}
            >
              <Typography variant="h6" fontWeight="600" mb={2}>
                Customer Search
              </Typography>

              {/* Main Row */}
              <form onSubmit={handleSearch}>
                <Box display="flex" alignItems='flex-start' gap={1.5} >
                  {/* Icon Box */}
                  <Box
                    sx={{
                      width: 40,
                      height: 40,
                      //   border: '1px solid #d0d0d0',
                      borderRadius: 1,
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      color: 'var(--bs-primary1)',
                    }}
                  >
                    <SearchIcon />
                  </Box>

                  {/* Input Field */}



                  <Autocomplete
                    freeSolo
                    options={suggestions}
                    getOptionLabel={(option) =>
                      typeof option === 'string' ? option : option.customer_number
                    }
                    inputValue={mobile}
                    onInputChange={handleInputChange}
                    fullWidth
                    renderInput={(params) => (
                      <CustomTextField
                        {...params}
                        label="Search customer mobile number"
                        variant="outlined"
                        size="small"
                        error={!!error}
                        helperText={error ? error : null}


                      />
                    )}
                  />

                  {/* Button */}
                  <Button
                    variant="contained"
                    color="primary"
                    disabled={!isValidIndianMobile(mobile)}
                    type='submit'

                    sx={{
                      height: 40,
                      borderColor: 'var(--bs-primary1)', // Custom colors
                      width: "45%",
                      backgroundColor: 'var(--bs-primary1)', // Custom colors
                      color: "white",

                    }}
                  >
                    Search
                  </Button>

                </Box>
              </form>
            </Box>
          </CustomBox>

        </>}

      {currentPage === 'send-money-page' &&
        <BankTransferMoneyTransferPage />}


      {currentPage === 'add-newBeneficiaryPage' &&
        <AddNewBeneficiaryPage />
      }

      {currentPage === 'add-customer' &&
        <AddCustomer />
      }

      {currentPage === 'otp-page' &&
        <OtpPopUp />
      }

      {currentPage === 'marchant-onboarding-otp' &&
        <MarchantOnboarding />
      }

      {currentPage === 'KYC-page' &&
        <KYCPage />
      }

      {currentPage === 'invoice' &&
        <InvoiceRecord />
      }

    </>



  );
};

export default NewBankTransfer;
