import React, { createContext, useContext, useState } from 'react'

const BankTransferContext = createContext()


const BankTransferProvider = ({ walletCall , children}) => {

    const [selectedBank, setSelectedBank] = useState();
    const [wadhValue , setWadhValue] = useState('')
    const [globalLoader , setGlobalLoader] = useState(false)
     const [mobile, setMobile] = useState('');
    const [customer , setCustomer] = useState({})
      const [allBank , setAllBank] = useState([])
      const [currentPage , setCurrentPage] = useState('money-transfer')
      const [invoiceId , setInvoiceId] = useState('')
  return (
     <BankTransferContext.Provider value={{walletCall ,invoiceId ,setInvoiceId, setWadhValue ,wadhValue, globalLoader ,setGlobalLoader,mobile ,setMobile, currentPage ,setCurrentPage, setCustomer ,customer,selectedBank , setSelectedBank , allBank , setAllBank}}>
         {children}
     </BankTransferContext.Provider>
  )
}

export const  useBankTransfer = ()=> useContext(BankTransferContext) 

export default BankTransferProvider