import { useState } from 'react';
import { DEVICE_CONFIGS } from '../utils/deviceConfig';




export const useFingerprintCapture = ({ wadhValue  }) => {
    const [fingerprintData, setFingerprintData] = useState(null);
    const [loading, setLoading] = useState(false);
  
    const getPidOptionsXML = () => {
      return `<?xml version='1.0'?><PidOptions ver='1.0'><Opts fCount='1' fType='2' iCount='0' pCount='0' format='0' pidVer='2.0' timeout='10000' posh='UNKNOWN' env='P' wadh="${wadhValue}" /><CustOpts></CustOpts></PidOptions>`;
    };
  
    const tryHostsAndPorts = async (hosts, ports, path) => {
      for (let host of hosts) {
        for (let port of ports) {
          try {
            const url = `${host}:${port}${path}`;
            const res = await fetch(url, {
              method: 'CAPTURE',
              headers: { 'Content-Type': 'text/xml' },
              body: getPidOptionsXML(),
            });
            if (res.ok) {
              const text = await res.text();
              return { success: true, data: text, url };
            }
          } catch (err) {
            continue; // try next port/host
          }
        }
      }
      throw new Error('No available host/port for fingerprint capture.');
    };
  
    const parseCaptureResponse = (xmlText) => {
      const parser = new DOMParser();
      const doc = parser.parseFromString(xmlText, 'text/xml');
      const resp = doc.getElementsByTagName('Resp')[0];
  
      const errCode = resp?.getAttribute('errCode');
      const errInfo = resp?.getAttribute('errInfo');
      const qScore = resp?.getAttribute('qScore');
  
      if (errCode !== '0') {
        throw new Error(errInfo || 'Unknown capture error');
      }
  
      return {
        status: 'Captured',
        captureSuccess: true,
        errCode,
        qScore,
        pidData: xmlText,
      };
    };
  
    const captureFingerprint = async (deviceName) => {
      setLoading(true);
      try {
        const device = DEVICE_CONFIGS?.find((d) => d.name.toLowerCase() === deviceName.toLowerCase());
        if (!device) throw new Error('Device config not found');
  
        const result = await tryHostsAndPorts(device.hosts, device.ports, device.path);
        const parsed = parseCaptureResponse(result.data);
        setFingerprintData(parsed);
        return parsed;
      } catch (err) {
        throw err;
      } finally {
        setLoading(false);
      }
    };
  
    return {
      fingerprintData,
      loading,
      captureFingerprint,
    };
  };
  